class SeamCarver {
    image = null;
    energy = [];
    M = [];
    backtrack = [];

    constructor(image) {
        this.image = image;
        this.energy = [];
        this.M = [];
        this.backtrack = [];
    }

    init() {
    }

    calculateEnergy() {
        let sobelX = [
            [1, 0, -1],
            [2, 0, -2],
            [1, 0, -1],
        ];
        let sobelY = [
            [1, 2, 1],
            [0, 0, 0],
            [-1, -2, -1],
        ];
        let energyBuffer = [];

        for (let y = 0; y < this.image.height; y++) {
            for (let x = 0; x < this.image.width; x++) {
                let energy = Math.abs(this.convolveGrayPixel3x3(x, y, sobelX, this.image.data)) + Math.abs(this.convolveGrayPixel3x3(x, y, sobelY, this.image.data));
                energyBuffer.push(energy);
            }
        }
        this.energy = energyBuffer
    }

    convolveGrayPixel3x3(x, y, filter2d, buffer) {

        let pix = this.getPixel(x, y, buffer, true);
        let u = this.getPixel(x, y - 1, buffer, true);
        let ur = this.getPixel(x + 1, y - 1, buffer, true);
        let r = this.getPixel(x + 1, y, buffer, true);
        let br = this.getPixel(x + 1, y + 1, buffer, true);
        let b = this.getPixel(x, y + 1, buffer, true);
        let bl = this.getPixel(x - 1, y + 1, buffer, true);
        let l = this.getPixel(x - 1, y, buffer, true);
        let ul = this.getPixel(x - 1, y - 1, buffer, true);

        let result = parseInt(filter2d[0][0] * this.getGrayValue(ul) + filter2d[0][1] * this.getGrayValue(u) + filter2d[0][2] * this.getGrayValue(ur) +
            filter2d[1][0] * this.getGrayValue(l) + filter2d[1][1] * this.getGrayValue(pix) + filter2d[1][2] * this.getGrayValue(r) +
            filter2d[2][0] * this.getGrayValue(bl) + filter2d[2][1] * this.getGrayValue(b) + filter2d[2][2] * this.getGrayValue(br))


        return result
    }

    getGrayValue(pixel) {
        return parseInt((pixel[0] + pixel[1] + pixel[2]) / 3)
    }

    getPixel(x, y, buffer = false, duplicateMissingPixel = false, logxy = false) {
        let offsetX = 0;
        let offsetY = 0;

        if (duplicateMissingPixel) {
            if (x < 0) offsetX = 1;
            else if (x === this.image.width) offsetX = -1;
            if (y < 0) offsetY = 1;
            else if (y === this.image.height) offsetY = -1;
        }

        let index = ((y + offsetY) * this.image.width + (x + offsetX)) * 4;
        if (logxy) console.log("x,y", x, y, index, buffer);

        return [
            buffer[index],
            buffer[index + 1],
            buffer[index + 2],
            buffer[index + 3]
        ]
    }

    removePixel(index, buffer, rgba = true) {
        if (rgba) {
            buffer.splice(index + 3, 1);
            buffer.splice(index + 2, 1);
            buffer.splice(index + 1, 1);
        }
        buffer.splice(index + 0, 1);
    }

    calculateMinimumSeam() {
        let M = Array.from(this.energy);
        let backtrack = new Array(this.image.width * this.image.height).fill(0);
        for (let y = 1; y < this.image.height; y++) {
            for (let x = 0; x < this.image.width; x++) {
                let ul = M[((y - 1) * this.image.width) + (x - 1)];
                let u = M[((y - 1) * this.image.width) + (x)];
                let ur = M[((y - 1) * this.image.width) + (x + 1)];

                let possiblePreviousPixels = [];
                if (x === 0) possiblePreviousPixels.push(u, ur);
                else if (x === this.image.width - 1) possiblePreviousPixels.push(ul, u);
                else possiblePreviousPixels.push(ul, u, ur);

                let lowestEnergyValueIndex = possiblePreviousPixels.indexOf(Math.min(...possiblePreviousPixels));
                backtrack[y * this.image.width + x] = x + lowestEnergyValueIndex - 1;
                M[y * this.image.width + x] += possiblePreviousPixels[lowestEnergyValueIndex];
            }
        }

        this.backtrack = backtrack;
        this.M = M;
    }

    carve(resize) {
        console.log('- start carving -')

        // this.calculateEnergy();
        // this.calculateMinimumSeam();

        let totalCarves = resize.width + resize.height;
        let carveCount = 0;

        for (let x = 0; x < resize.width; x++) {
            this.carveColumn()
            carveCount++;
            self.postMessage({
                action: "percentage",
                percentage: carveCount / totalCarves
            });
        }

        if (resize.height > 0) {
            this.rotate();
            // this.calculateEnergy();
            // this.calculateMinimumSeam();
            for (let x = 0; x < resize.height; x++) {
                this.carveColumn();
                carveCount++;
                self.postMessage({
                    action: "percentage",
                    percentage: carveCount / totalCarves
                });
            }
            this.rotate();
        }

        console.log('- carving finished -')

        return {
            "height": this.image.height,
            "width": this.image.width,
            "data": this.image.data,
            "M": this.M,
            "backtrack": this.backtrack
        }
    }

    rotate() {
        let imageRotated = [];
        let transposedEnergy = [];
        for (let x = 0; x < this.image.width; x++) {
            for (let y = 0; y < this.image.height; y++) {
                const i = ((y * this.image.width) + x) * 4;
                imageRotated.push(
                    this.image.data[i],
                    this.image.data[i + 1],
                    this.image.data[i + 2],
                    this.image.data[i + 3]
                );
                transposedEnergy.push(
                    this.energy[i]
                )
            }
        }

        this.image.data = imageRotated;
        const tmpWidth = this.image.width;
        this.image.width = this.image.height;
        this.image.height = tmpWidth;

        this.energy = transposedEnergy;
    }

    carveColumn() {
        this.calculateEnergy();
        this.calculateMinimumSeam();

        let lastRowM = this.M.slice((this.image.height - 1) * this.image.width, (this.image.height) * this.image.width);
        let startXIndex = lastRowM.indexOf(Math.min(...lastRowM));

        let seamCarveBuffer = Array.from(this.image.data);
        for (let y = this.image.height - 1; y >= 0; y--) {
            let index = y * (this.image.width) + startXIndex;

            // this.setPixel(index * 4, seamCarveBuffer, 255,0,0);

            this.carvePixel(index, seamCarveBuffer);
            startXIndex = this.backtrack[index];
        }

        this.image.width -= 1;
        this.image.data = seamCarveBuffer;
    }

    carvePixel(index, buffer) {
        this.removePixel(index * 4, buffer);
        this.removePixel(index, this.energy, false);
        this.removePixel(index + 1, this.M, false); // why +1 ????
        this.removePixel(index + 1, this.backtrack, false); // why +1 ????
    }

    setPixel(index, buffer, r, g, b) {
        buffer[index] = r;
        buffer[index + 1] = g;
        buffer[index + 2] = b;
        buffer[index + 3] = 255;
    }
}

self.addEventListener('message', (event) => {
    if (event.data.action !== 'seam-carving') return;

    const data = event.data;
    let seamCarver = new SeamCarver(data.image);
    seamCarver.init();

    var result = {
        image: seamCarver.carve(data.resize),
        action: 'done'
    };

    self.postMessage(result);

}, false);
